import React, { useState, useEffect } from 'react'
import MultiLineChart from './multilinechart';
import {  DownOutlined } from '@ant-design/icons'
import { Button, Input, Row, Col, Dropdown, Space ,Card,InputNumber} from 'antd'
import axios from 'axios'
import './chartcontainer.css'
const color = {};
const ChartContainer = () => {
    const [updatetime, setupdatetime] = useState(2);
    const [timecount, setimecount] = useState(0);
    const [items, setItems] = useState([]);
    const [curaddress, setCurAddress] = useState('');
    const [timeid, setTimeId] = useState(0);
    const [disaddress, setDisAddress] = useState('');
    const [hisdata, setHisData] = useState({});
    const [cData,setCData] = useState({labels:[],datasets:[]})
    useEffect(() => {
        getAddresses();
    }, []);
    const getAddresses = async () => {
        let res;
        try{
        res = await axios.get('http://54.160.184.59/api/addresses/', { 'header': { 'content-type': 'application/json' } });
        setItems(
            res.data.map((address, index) => {
                return (
                    {
                        key: index,
                        label: (
                            <p onClick={changeAddress}>
                                {address.address}
                            </p>
                        ),
                    })

            })
        )
        }
        catch(except)
        {
            console.log(except);
        }
        
    }

    const handleAddressChange = (event) => {
        setCurAddress(event.target.value)
    }

    const onSubmit = () => {
        setDisAddress(curaddress);
        if (timeid)
            clearInterval(timeid);
        setTimeId(setInterval(() => {
            setimecount((prevCount) => prevCount + updatetime);
        }, updatetime * 1000));
    }

    const onUpdateTimeChange = (value) => {
        setupdatetime(value);
    }
    const getData = async () => {
        const payload = {
            address: disaddress
        }
        const res = await axios.post(`http://54.160.184.59/api/addresses/`, payload);
        console.log(res);
        let pData = { ...hisdata };

        if (pData["time"] === undefined)
            pData = { time: ['0'] };
        else {
            pData.time.push(''+timecount)
            Object.keys(pData).forEach(address => {
                if (address !== 'time')
                    pData[address].push(0);
            })
        }

        Object.keys(res.data).forEach(address => {
            const percent = res.data[address]['percentages'][0];
            const timesarray = pData.time;
            let dataarray = pData[address];
            if (dataarray === undefined && timesarray.length !== 1) {
                dataarray = [];
                for (let i = 0; i < timesarray.length; i++)
                    dataarray.push(0);
            }
            else {
                if (timesarray.length === 1) dataarray = []
                dataarray[timesarray.length - 1] = percent;
            }
            pData[address] = dataarray;
        });
        setHisData(pData);

        const datasets = [];
        for (const address in hisdata) {
            if(address !== 'time')
            datasets.push({
                label: address,
                data: hisdata[address],
                fill: false,
                borderColor: getborderColor(address),
                tension: 0.1
            })
        }
        const chartdata = {
            labels: hisdata.time,
            datasets: datasets
        }
        setCData(chartdata);
    }
    const changeAddress = (e) => {
        setCurAddress(e.target.innerText);
    //  document.getElementById('submit').click();
    }

    const getborderColor = (username) => {
        if (color[username] === undefined)
            color[username] = `rgb(${Math.random() * 1000 % 255},${Math.random() * 1000 % 255},${Math.random() * 1000 % 255})`;
        return color[username];
    }
    useEffect(() => {
         if (timecount)
            getData();
    }, [curaddress, timecount])
    return (
        <>
            <Card className='ChartContainer' >
                <Row >
                    <Col span={3}>
                        <h1>Address:</h1>
                    </Col>
                    <Col span={5}>
                    <Input className='input'  value={curaddress} onChange={handleAddressChange} />
                    </Col>
                    <Col  span={3}>
                        <h1>UpdateTime:</h1>
                    </Col>
                    <Col  span={1}>
                        <InputNumber className='input' name='updatetime'  value={updatetime} onChange={onUpdateTimeChange}></InputNumber>
                    </Col>
                    <Col span={3} >
                        <Button className='submit' onClick={onSubmit}>Submit</Button>
                    </Col>
                </Row>
                <Row>
                <Col style={{marginTop:'10px'}} span={3}>
                    <Dropdown className='dropdown' menu={{ items }} trigger={['click']}>
                        <Button >
                            <Space>
                                Address
                                <DownOutlined />
                            </Space>
                        </Button>
                    </Dropdown>
                    </Col>
                </Row>
                <MultiLineChart data={cData} />
            </Card>
        </>

    )
}
export default ChartContainer;