import React from 'react';
import { Line } from 'react-chartjs-2'; // Import the Line chart from react-chartjs-2
import { Chart as ChartJS, CategoryScale, LinearScale, LineElement, PointElement, Title, Tooltip, Legend } from 'chart.js';

// Register the necessary components in Chart.js
ChartJS.register(
  CategoryScale,
  LinearScale,
  LineElement,
  PointElement,
  Title,
  Tooltip,
  Legend
);

const MultiLineChart = (props) => {
  // Data for the multi-line chart
  // Options to configure the chart (optional)

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top', // Position of the legend
      },
      tooltip: {
        mode: 'index',
        intersect: false, // Show tooltip when the mouse intersects the chart
      }
    },
    scales: {
      x: {
        title: {
          display: true,
          text: 'Time' // X-axis title
        }
      },
      y: {
        title: {
          display: true,
          text: 'Percentage' // Y-axis title
        },
        beginAtZero: true, // Start the Y-axis at zero
      }
    }
  };
  return (
    <div>
      {/* <h2>Chart App</h2> */}
      <Line data={props.data} options={options} /> {/* Render the chart */}
    </div>
  );
};

export default MultiLineChart;